* {
    box-sizing: border-box;
}

html {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 17px;
}

body {
    margin: 0;
    min-height: 100vh;
    height: 1px;
    text-align: center;
}

h1 {
    margin-top: 0;
}

.btn.female {
    background-color: pink;
}

.male, .female {
    max-width: 260px;
    width: 100%;
}

#app {
    width: 100%;
    padding: 15px;
}

.stage {
    max-width: 760px;
    margin: auto;
    padding-top: 40px;
    height: 100%;
}

#mapca {
    max-width: 200px;
}

.stage label {
    cursor: pointer;
    display: block;
    border: 1px solid #000;
    max-width: 350px;
    width: 100%;
    padding: 15px;
    padding-left: 40px;
    margin: 15px auto;
}

img {
    display: block;
    margin: auto;
    max-width: 500px;
    width: 100%;
}

.list {
    text-align: left;
    list-style: none;
}

.list p {
    margin: 0;
    font-size: 1.3rem;
    font-weight: bold;
}

.list span {
    font-size: 1.2rem;
    line-height: 2rem;
}

p {
    font-size: 1.2rem;
}

.stage .subtitle {
    font-size: 1.5rem;
    text-align: center;
}

.list li{
    margin-bottom: 20px;
    padding-top: 3px;
    padding-right: 25px;
    padding-left: 40px;
    background-image: url(https://d33wubrfki0l68.cloudfront.net/images/2fd5acd06ce87032bfe3a69c127772589d455382/checked.png);
    background-position: 0 0;
    background-size: 25px 25px;
    background-repeat: no-repeat;
    background-attachment: scroll;
    text-align: left;
}

.btn {
    display: inline-block;
    padding: 15px 25px;
    background-color: #3898ec;
    color: #fff;
    border: 0;
    line-height: inherit;
    text-decoration: none;
    cursor: pointer;
    border-radius: 0;
    font-size: 1.5rem;
    margin: 15px;
}

.stg_ans {
    max-width: 400px;
    width: 100%;
    padding: 15px;
    margin: 15px auto;
    cursor: pointer;
    display: block;
}

.stg_ans:hover, .stg_ans:focus {
    background-color: blue;
}

.btn.yes {
    background-color: green;
}

.stg4 .btn.yes {
    max-width: 450px;
    width: 100%;
    margin: 15px auto;
}

.stage p {
    text-align: left;
}

.btn.no {
    background-color: red;
}

.checkbox {
    display: block;
    position: relative;
    padding-left: 50px !important;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: left;
}

.checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 50%;
    left: 15px;
    height: 25px;
    width: 25px;
    background-color: #eee;
    transform: translateY(-50%);
}

.checkbox:hover input ~ .checkmark {
    background-color: #ccc;
}

.checkbox input:checked ~ .checkmark {
    background-color: #2196F3;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox input:checked ~ .checkmark:after {
    display: block;
}

.checkbox .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.input_number label {
    border: none;
    font-size: 1.3rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    max-width: 350px;
    width: 100%;
    min-height: 60px;
}
.input_number label p {
    margin: 10px 5px;
    text-align: right;
}

.input_number label input {
    font-size: 1em;
    width: 60px;
}

.s11.input_number label {
    max-width: 500px;
}

.s11.input_number label p {
    max-width: 180px;
    width: 100%;
    text-align: center;
    margin: 5px auto;
}

.btn:disabled {
    background-color: grey;
    cursor: not-allowed;
}

.stg13 img {
    width: 100%;
    max-width: initial;
}
.stg13 p {
    text-align: center;
}

.red {
    color: #ff0004;
    font-weight: bold;
}

.metabolic {
    display: flex;
    justify-content: center;
    width: 100%;
}

.metabolic p {
    width: 100%;
    font-weight: bold;
}

.flex {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

.flex-item {
    flex: 1 1 175px;
    font-size: 1.3rem;
    padding: 20px 10px;
}

.cards {
    box-shadow: 0 0 10px grey;
    border-radius: 5px;
    flex: 1 1 300px;
}

.similar {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.flex-item .red {
    font-size: 3rem;
}

.flex-item h3 {
    margin: 0 auto;
}

.week {
    display: flex;
}

.week-day {
    width: calc(100% / 7);
}

.week-day p {
    font-size: .9rem;
}